import React, { Component } from 'react';
import {
    Form, Icon, Input, DatePicker, Button, message
} from 'antd';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { JSEncrypt } from 'jsencrypt'
import './register.css';
import Ajax from '../../component/ajax';

class Register extends Component {
    constructor(props) {
        super(props);
        this.state = {
            regist: false
        }
        this.checkUser = this.checkUser.bind(this);
    }
    checkUser(e) {
        let user = e.target.value,
            _self = this;
        Ajax.post('/findUser', {
            username: user
        }).then(res => {
            if (res.data.msg === 200) {
                _self.setState(() => ({
                    regist: false
                }), () => {
                    message.error('');
                });
            } else {
                _self.setState(() => ({
                    regist: true
                }));
            }
        }).catch(err => { console.log(err) });
    }
    handleSubmit = (e) => {
        let _self = this;
        let encrypt = new JSEncrypt();
        encrypt.setPublicKey(_self.props.publicPem);
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if (_self.state.regist) {
                    Ajax.post('/register', {
                        username: values.username,
                        password: encrypt.encrypt(values.password),
                        phone: values.phone,
                        birthday: values.birthday.format('YYYY-MM-DD'),
                        email: values.email
                    }).then(res => {
                        if (res.data.msg === 200) {
                            _self.setState(() => ({
                                regist: false
                            }), () => {
                                message.success(',');
                            });
                        } else {
                            message.err(res.data.data);
                        }
                    }).catch(err => { console.log(err) });
                }
            }
        });
    }
    render() {
        const { getFieldDecorator } = this.props.form;

        const config = {
            rules: [{ type: 'object', required: true, message: '' }],
        };
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };
        return (
            <div className='register-page'>
                <div className='register'>
                    <Link to='/login'></Link>
                    <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                        <Form.Item label=":">
                            {getFieldDecorator('username', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input onBlur={this.checkUser} prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="" />
                            )}
                        </Form.Item>
                        <Form.Item label=":">
                            {getFieldDecorator('password', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="" />
                            )}
                        </Form.Item>
                        <Form.Item label=":">
                            {getFieldDecorator('email', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input prefix={<Icon type="mail" style={{ color: 'rgba(0,0,0,.25)' }} />} type="email" placeholder="" />
                            )}
                        </Form.Item>
                        <Form.Item label=":">
                            {getFieldDecorator('phone', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input prefix={<Icon type="phone" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="" />
                            )}
                        </Form.Item>
                        <Form.Item
                            label=":"
                        >
                            {getFieldDecorator('birthday', config)(
                                <DatePicker placeholder="" />
                            )}
                        </Form.Item>
                        <Button size='large' className="register-submit" type="primary" htmlType="submit">
                            
                        </Button>
                    </Form>
                </div>
            </div>
        )
    }
}

export default connect((state => ({
    publicPem: state.publicPem
})), null)(Form.create()(Register)); 